.knowledge-section {
    padding: 50px;
    text-align: center;
  }
  
  .knowledge-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    margin-top: 20px;
    
  }
  
  .knowledge-card {
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    transition: transform 0.3s ease;
    background-color:#E5F3FE;
  }
  
  .knowledge-card:hover {
    transform: scale(1.05);
    background-color:#357A98;
  }
  
  .knowledge-icon {
    width: 50px;
    margin-bottom: 10px;
  }
  
  .knowledge-link {
    display: inline-block;
    margin-top: 10px;
    color: #357A98;
    font-size: 1.5rem;
    text-decoration: none;
  }
  
  .search-bar {
    margin-top: 30px;
  }
  
  .search-input {
    padding: 10px;
    width: 250px;
    font-size: 1rem;
    border-radius: 25px;
    border: 1px solid #ccc;
    margin-right: 10px;
  }
  
  .search-button {
    padding: 10px 20px;
    font-size: 1rem;
    background-color: #357A98;
    color: white;
    border: none;
    border-radius: 25px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .search-button:hover {
    background-color: #2a5f79;
  }
  
  .search-results {
    margin-top: 20px;
  }
  
  .search-hint {
    margin-top: 40px;
    font-style: italic;
    color: #555;
  }
  